.posts{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.posts div{
    flex: 0 25%;
}
@media only screen and (max-width:1200px) {
    .posts div{
        flex: 0 35%;
    } 
}
@media only screen and (max-width:700px) {
    .posts div{
        flex: 0 90%;
    } 
}