.mode{
  position: fixed;
  top :6vh;
  right: 2vw;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-items: center;
  z-index: 2000;
}
.mode button{
 
  background-color: rgba(241, 227, 227, 0.0);
  cursor: pointer;
  border: 0px;
}

body {
  background-color:#111;
  color: #fff;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 100%;
  height: fit-content;
  font-family: 'Poppins';font-size: 22px;
  padding: 0;
}
.cont{
  max-width: 100%;
  min-height: 100vh;
  position: relative;
  margin: 0;
  padding: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.scolor{
  background-color: #ffb400 !important;
}
.scolor-font{
  color: #ffb400;
}
.rtl{
  direction: rtl;
}
.ltr{
  direction: ltr;
}