
.home-cont{
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: 100vh;
}


.personal-img{
    /* */
    height: 100vh;
    width: 37%;
    position: relative;
    
}
.personal-img img{
    height: calc(100vh - 80px);
    width: calc(100% - 40px);
    /* border: 1px solid rgb(177, 50, 50); */
    border-radius: 5px;
    box-shadow: -1px -1px 7px 0px white;
    transform: rotate3d(0,04,1,-20deg);
    object-fit: cover;
    position: absolute;
    top:40px;
    left:20px;
}
.personal-info{
    width:calc(63%);
    height: 100vh;
    position: relative;
}
.home-header{
    font-size: 4vw;
    line-height: 52px;
    position: absolute;
    top:20%;
    right: 20%;
    text-transform: uppercase;

}
.home-header span{
    color:#fff ;
}
.personal-info p{
    position: absolute;
    top:50%;
    width: 60%;
    font-family: Open Sans,sans-serif;
    font-size: 12px;
    line-height: 30px;
    right: 20%;
    font-size: 15px;
}
.personal-info button{
    position: absolute;
    border-radius: 30px;
    top:70%;
    left: 20%;
    width: 15vw;
    height: 50px;
    line-height: 20px;
    background-color: rgba(255, 255, 255, 0.0);
    cursor: pointer;
    border : 1px solid #ffb400;
    font-size: 30px;
    color: #ffb400;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    
}
.personal-info button h6{
margin-right: 30px;
}

.btn-icon{
    display: inline-block;
    width: 50px;
    height: 98%;
    background-color: #ffb400;
    border-radius: 30px;
    position: absolute;
    right: 0px;
}
.personal-info button i::after{
    content: url(../../icons/person.svg);
    display: inline-block;
    width: 30px;
    height: 30px;
    margin-top: 1vh;
    border-radius: 15px;

}
.personal-info button:hover{
    background-color: #ffb400;
    color: #fff;
}
@media only screen and (max-width:900px) {
    .home-cont{
        flex-direction: column;
        height: fit-content;
        justify-content: center;
        align-items: center;
        max-width: 100%;
        padding: 5vh 0px 0 0px;
    }
    .personal-img{
        height: 200px;
        width: 200px;
        border-radius: 50%;
    }
    .personal-img img{
        height: 98%;
        width: 98%;
        border-radius: 50%;
        position:initial;
    }
    .personal-info{
        width:100%;
        height: fit-content;
        position: initial;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .home-header{
        font-size: 4vw;
        line-height: 52px;
        position: relative;
        font-size: 30px;
        text-align: center;
        text-transform: uppercase;
        right: 0;
        max-width: 100vw;
    
    }
    .personal-info p{
        /* width: 100%; */
        position:initial;
        width: 90%;
        font-size: 15px;
        text-align: center;
        display: flex;
        flex-wrap: wrap;
         
    }
    .personal-info button{
        position: relative;
        border-radius: 30px;
        top:0;
        left: 0;
        width: 200px;
        height: 50px;      
        
    }
   
}
